<template>
  <div class=" text-center loginscreen  animated fadeInDown login">
    <div class="row mt">
      <h1>用户注册</h1>
      <div class="m-t form-horizontal middle-box">
        <form class="form-horizontal" @submit.prevent="register" id="form">
          <div class="form-group">
            <label class="control-label col-sm-3"><span class="MustFill">*</span>用户名</label>
            <div class="col-sm-9">
              <input type="text" class="form-control"  required="" aria-required="true"  name="username" v-model="form.username" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
            </div>
          </div>
          <div class="form-group">
            <label class="control-label col-sm-3"><span class="MustFill">*</span>真实姓名</label>
            <div class="col-sm-9">
              <input type="text" class="form-control"  required="" aria-required="true"  name="name" v-model="form.name">
            </div>
          </div>
          <div class="form-group">
            <label class="control-label col-sm-3"><span class="MustFill">*</span>联系电话</label>
            <div class="col-sm-9">
              <input type="text" maxlength="11" class="form-control" required="" aria-required="true" name="phone" v-model="form.phone" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
              <p>(请输入11位数的电话号码)</p>
            </div>
          </div>
          <div class="form-group">
            <label class="control-label col-sm-3"><span class="MustFill">*</span>邮箱</label>
            <div class="col-sm-9">
              <input type="email" class="form-control"  required="" aria-required="true"  name="email" v-model="form.email">
            </div>
          </div>
          <div class="form-group">
            <label class="control-label col-sm-3"><span class="MustFill">*</span>密码</label>
            <div class="col-sm-9">
              <input type="password" class="form-control" required="" aria-required="true"  name="password" v-model="form.password" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
              <p>(请输入6位以上的字母加数字)</p>
            </div>
          </div>
          <div class="form-group">
            <label class="control-label col-sm-3"><span class="MustFill">*</span>确认密码</label>
            <div class="col-sm-9">
              <input type="password" class="form-control" required="" aria-required="true"  name="password2" v-model="password" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
            </div>
          </div>
          <div class="col-sm-offset-3 col-sm-9">
            <button type="submit" class="btn btn-primary block full-width">注册</button>

            <b class="btn btn-white block full-width" @click="cancel">返回登录页</b>
          </div>
        </form>
      </div>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
import Vue from 'vue'
import { API_URL } from '@/assets/config'
import Cache from '@/assets/cache'
import dataService from '@/assets/method/data'
export default {
  name: 'register',
  data () {
    return {
      password: '',
      form: {},
      canSubmit: true,
      validate: null
    }
  },
  mounted () {
    this.validate = dataService.validate('#form') // 表单验证
  },
  methods: {
    cancel () {
      this.$router.push('/login')
      return
    },
    register () {
      if (!this.canSubmit) return
      if (!$('#form').valid()) {
        toastr.warning('请完善提交数据')
        return
      }
      if (!this.form.phone || (this.form.phone && this.form.phone.length != 11)) {
        toastr.warning('请输入11位数的电话号码')
        return
      }
      if (this.form.password == Number(this.form.password) || this.form.password.length < 6) {
        toastr.warning('请输入6位以上的字母加数字')
        return
      }
      if (this.password != this.form.password) {
        toastr.warning('两次输入的密码不一致')
        return
      }
      this.canSubmit = false
      Vue.http.options.headers = {}
      Vue.http.post(API_URL + 'register', this.form).then(response => {
        if (response.data.code == 200) {
          toastr.success('注册成功,请联系管理员启用')
          setTimeout(() => this.$router.push('/login'), 1000)
        } else {
          this.canSubmit = true
          toastr.error(response.data.msg)
        }
      }).catch(error => {
        this.canSubmit = true
      })
    }
  }
}
</script>

<style scoped>
  .mt{
    margin-top: 100px;
  }
  .mt h1{
    font-weight: bold;
  }
  .login{
    position: relative;
    padding: 3rem;
    height: 100vh;
    background-repeat: no-repeat;
    font-size: 16px;
    color:#000
  }
  .btn-white {
    margin-top: 10px;
  }
  p {
    margin-bottom: 0;
    color: #999;
    font-size: 12px;
  }
</style>



